body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: white;
  color: #1a1a1a;
}

.App {
  max-width: 1100px;
  margin: 0 auto;
  padding: 20px;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #eee;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  color: #4a632d;
}

.nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.basket {
  background-color: #4a632d;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}

/* Main title */
main h1 {
  font-size: 36px;
  margin: 40px 0 20px;
}

/* Main content layout */
.content {
  display: flex;
  gap: 40px;
  align-items: flex-start;
}

/* Service cards list */
.services {
  flex: 2;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Individual service card */
.service-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fafaf3;
  border-radius: 16px;
  padding: 16px 20px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.05);
}

.left {
  display: flex;
  align-items: center;
  gap: 16px;
}

.icon {
  width: 60px;
  height: 60px;
  object-fit: contain;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
}

.details {
  background: white;
  border: 1px solid #ccc;
  padding: 6px 12px;
  border-radius: 20px;
  cursor: pointer;
}

/* Calendar box */
.calendar-box {
  background-color: #fafaf3;
  padding: 20px;
  border-radius: 16px;
  width: 280px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.05);
}

.calendar-box h3 {
  margin-top: 0;
}

.calendar {
  background: white;
  height: 160px;
  margin: 10px 0;
  border: 1px solid #ccc;
}

.book-btn {
  background-color: #4a632d;
  color: white;
  border: none;
  padding: 10px;
  width: 100%;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
}

/* Footer */
.footer {
  text-align: center;
  padding: 30px 0;
  color: #666;
  border-top: 1px solid #ddd;
  margin-top: 60px;
}
